.finance-page {
    width: 100%;
}

.account-summary-top {
    display: flex;
    gap: var(--spacing-xl);
    padding: var(--spacing-lg);
    margin-bottom: var(--spacing-lg);
}

.summary-item {
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.summary-item .label {
    font-size: 0.75rem;
    color: var(--secondary);
    text-transform: uppercase;
    letter-spacing: 0.1em;
}

.summary-item .value {
    font-size: 1.5rem;
    font-weight: 700;
}

.summary-item .value.positive {
    color: var(--fintech-green);
}

.finance-grid {
    display: grid;
    grid-template-columns: 1fr 340px;
    gap: var(--spacing-lg);
}

.chart-main {
    height: 600px;
}

.finance-side {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-lg);
}

.trading-tools,
.market-stats {
    padding: var(--spacing-lg);
}

.trading-tools h3,
.market-stats h3 {
    font-size: 1rem;
    margin-bottom: var(--spacing-md);
    color: var(--secondary);
    text-transform: uppercase;
}

.tool-list {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
}

.tool-card {
    display: flex;
    gap: var(--spacing-md);
    padding: var(--spacing-md);
    background: rgba(255, 255, 255, 0.02);
    border-radius: var(--border-radius-md);
    border: 1px solid var(--glass-border);
    transition: var(--transition-smooth);
}

.tool-card:hover {
    background: rgba(255, 255, 255, 0.05);
}

.tool-card h4 {
    font-size: 0.9rem;
    margin-bottom: 2px;
}

.tool-card p {
    font-size: 0.75rem;
    color: var(--secondary);
}

.sentiment-bar {
    height: 32px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: var(--border-radius-full);
    overflow: hidden;
    display: flex;
    margin-bottom: var(--spacing-md);
}

.sentiment-bar .bullish {
    background: var(--fintech-green);
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.7rem;
    font-weight: 800;
}

.sentiment-bar .bearish {
    background: var(--fintech-red);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.7rem;
    font-weight: 800;
}

.mini-stats {
    display: flex;
    justify-content: space-between;
}

.mini-stats .stat {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 0.75rem;
    color: var(--secondary);
}

@media (max-width: 1024px) {
    .finance-grid {
        grid-template-columns: 1fr;
    }
}